@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  background: linear-gradient(45deg, lightblue, pink);
}

label {
  color: #6d6a6a;
}

input {
  padding: 5px;
}

input:focus:invalid {
  box-shadow: 0 0 5px 3px red;
}

input:focus:valid {
  box-shadow: 0 0 7.5px 4.5px green;
}

.flex-col {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.flex-col-left {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
}

.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.modal {
  padding: 50px 20px;
  margin: 10vh auto 0 auto;
  border-radius: 5px;
  background: white;
  font-family: Roboto, 'sans-serif';
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.do-not-render {
  display: none;
}

.home {
  min-height: 400px;
  height: 60vh;
  min-width: 300px;
  width: 25vw;
  text-align: center;
}

.longer {
  height: 75vh;
}

#welcome {
  padding: 5px;
  margin: 5px;
}

#product {
  width: 200px;
  padding: 10px;
}

#submit {
  padding: 10px 30px;
  border: 0;
  border-radius: 10px;
  margin: 20px auto 0 auto;
  color: white;
  font-weight: bold;
  font-size: 15px;
  background: #f5af19;
  transition: background 0.5s;
}

#submit:hover {
  cursor: pointer;
}

#submit:active {
  background: grey; 
}

.search {
  width: 500px;
  margin: 10vh auto;
  font-family: Roboto;
}

.result {
  min-height: 200px;
  height: 40vh;
  min-width: 470px;
  width: 25vw;
}

.product-price {
  color: brown;
}

.product-eff {
  width: 800px;
  height: 200px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

@media screen and (max-width: 850px) {
  .product-eff {
    width: 100%;
  }
}

.energy-info {
  background: white;
  padding: 10px;
  border-radius: 100px;
}

.dark {
  background: black;
  color: white;
}

a .dark {
  color: white;
}

.loading {
  font-family: Roboto;
  font-size: 30px;
  margin-top: 10vh;
}

.back {
  position: fixed;
  left: 30px;
  top: 30px;
  border-radius: 100px;
  width: 50px;
  height: 50px;
  background: white;
}

.back:hover {
  cursor: pointer;
}

.back > img {
  width: 40px;
  height: 40px;
  margin: 5px;
}

